import styled from 'styled-components';

const sidePadding = `16px`;
const borderRadius = `10px`;

export const Container = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;

    p {
        background: #f0f0f0;
        padding: 1rem ${sidePadding};
        border-radius: ${borderRadius} 0px 0px ${borderRadius};
    }

    button {
        background: #1b72e8;
        color: white;
        border: none;
        font-size: 1.4rem;
        cursor: pointer;
        height: 3.6rem;
        border-radius: 0px ${borderRadius} ${borderRadius} 0;
        padding: 0 ${sidePadding};
    }

    @media (max-width: 478px) {
        flex-direction: column;
        font-size: 0.85rem;

        p {
            border-radius: ${borderRadius};
        }

        button {
            font-size: 1rem;
            border-radius: ${borderRadius};
            padding: 0 3rem;
            height: 2.5rem;
        }
    }
`;
